/* reset */
* {
    margin: 0; 
    padding: 0;
}
div {
    box-sizing: border-box;
}
input {
    height: 40px; 
    width: 100%; 
    padding: 0 15px; 
    border: 1px solid #ddd; 
    border-radius: 3px; 
    box-sizing: border-box;
}
textarea {
    padding: 15px; 
    width: 100%; 
    height: 100px; 
    border: 1px solid #ddd; 
    box-sizing: border-box;
}
button {
    display: inline-block; 
    cursor: pointer;
}
select {
    display: inline-block; 
    height: 35px; 
    border: 1px solid #ddd; 
    box-sizing: border-box; 
    border-radius: 3px; 
    padding: 0 10px; 
    cursor: pointer;
}

/* nav */
.black_nav {
    background: black;
    width: 100%;
    display: flex;
    color: white;
    padding: 20px;
}

main {
    padding: 20px; 
    box-sizing: border-box;
}

/* form */
.form {
    margin-bottom: 40px;
}
.input_wrap {
    margin-bottom: 10px;
}
.btn_submit {
    line-height: 35px; 
    background: #fff; 
    border: 1px solid #ddd; 
    padding: 0 18px; 
    border-radius: 3px;
}
.btn_submit:hover {
    background: #f8f8f8;
}
.sort_wrap {
    text-align: right; 
    margin-bottom: 10px;
}

/* list */
.list {
    text-align: left;
    padding: 20px 20px 25px;
    border-bottom: 1px solid #ddd;
}
.list:nth-child(1) {
    border-top: 1px solid #ddd;
}
.title_wrap {
    display: flex; 
    justify-content: 
    space-between; 
    margin-bottom: 20px;
}
.list .title {
    cursor: pointer;
}
.btn_like {
    display: inline-block;
    background: #fff; 
    border: none;
    margin-right: 5px;
}
.count_like {
    font-size: 14px; 
    font-weight: bold;
}
.list .content {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    margin-bottom: 15px;
    color: #666;
    cursor: pointer;
}
.date {
    font-size: 14px; 
    color: #999; 
    text-align: right; 
    margin-bottom: 20px;
}
.btn_wrap {text-align: right;}
.btn_del {
    background: #fff; 
    border: 1px solid #ddd; 
    padding: 5px 10px; 
    border-radius: 3px;
}
.btn_del:hover {
    background: #f8f8f8;
}

/* modal */
.dimm {
    position: fixed; 
    left: 0; 
    top: 0; 
    width: 100%; 
    height: 100vh; 
    background: rgba(0, 0, 0, 0.5);
}
.modal {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 70%;
    height: auto;
    padding: 30px 30px 35px;
    background: #fff;
    min-height: 80vh;
    max-height: 80vh;
}
.modal .title {
    font-size: 1.17em; 
    margin-bottom: 5px;
}